import Password from '@/components/password/password.vue'

export default {
    name: "ForetIndex",
    data() {
        return {
            urlImage: require("@/assets/images/forest.png"),
            song: new Audio(require('@/assets/musics/forest.mp3')),
            isEnd: false,
        }
    },
    components: {
        Password
    },
    created() {
        // Si écran de fin, on charge les bonnes variables dont la musique
        if (this.$route.query.end) {
            this.isEnd = true;
            this.song =  new Audio(require('@/assets/musics/snape.mp3'));
        }
        this.playMusic();
    },

    unmounted() {
        this.song.pause();
    },

    methods: {

        passwordFound() {
            let password = document.getElementById("passwordForet");
            password.classList.add("disparition");

            let background = document.getElementById("imgForet");
            setTimeout(() => {
                background.classList.add("zoomIn")
            }, 2000);

            // 12 sec
            setTimeout(() => {
                this.$router.push('/chateau')
            }, 12000);

            // La musique
            this.decreaseSong();
        },

        playMusic() {
            this.song.play();
        },

        decreaseSong() {
            let timer = setInterval(() => {
                if (this.song.volume - 0.1 < 0) {
                    clearInterval(timer);
                } else {
                    this.song.volume -= 0.1;
                }
            }, 1000)
        }
    }
}